.estimate {
  padding: 50px 0;

  @media (max-width: 850px) {
    padding: 0;
  }

  &__title {
    margin: 0;
    margin-bottom: 50px;
    font-family: var(--second-family);
    font-weight: 500;
    font-size: 14px;
    letter-spacing: 0.07em;
    text-transform: uppercase;
    color: #000;
    text-align: center;

    @media (max-width: 850px) {
      margin-bottom: 30px;
    }
  }

  &__img {
    img {
      width: 100%;
      height: auto;
      object-fit: contain;
    }
  }
}